@import "colors";

nav {
 display: block;
 block-size: 4em;
 inline-size: 100%;
 position: fixed;
 top: 0;
 left: 0;
 background: $N1-10;
 transition: box-shadow .0875s linear;
 z-index: 1000;

 .container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  block-size: 100%;
 }

 &.scrolled {
  box-shadow: 0 0 transparent, 0 0 transparent, 0 1px 3px 0 #0000001a, 0 1px 2px -1px #0000001a;
 }

 .logo {
  display: inline-flex;
  align-items: center;
  gap: .25em;
  padding-block: .5em;
  font-weight: 300;
  color: $N1-900;
  position: relative;
  letter-spacing: .02em;
  text-transform: uppercase;

  strong {
   font-weight: 600;
   color: $A3-600;
  }
 }

 .nav-links {
  display: flex;
  align-items: center;

  li {
   display: inline-flex;

   a {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    font-weight: 475;
    color: $N2-700;
    position: relative;
    text-align: center;
    letter-spacing: .01em;

    &.active {
     color: $A3-600;
    }
   }
  }
 }

 &.nav-mobile {
  .container {
   padding-inline: .875em .5em;
  }

  .nav-links {
   flex-direction: column;
   justify-content: space-around;
   flex: 1;

   li {
    a {
     font-size: 1.25em;
    }
   }
  }

  aside {
   position: fixed;
   top: 0;
   left: -260px;
   inline-size: 260px;
   block-size: 100%;
   overflow: hidden;
   transition: all 200ms ease 0s;
   z-index: 1000;

   &.expanded {
    left: 0px;

    .scrim {
     visibility: visible;
    }
   }

   .scrim {
    position: fixed;
    inset: 0;
    block-size: 100%;
    inline-size: 100%;
    overscroll-behavior: contain;
    pointer-events: inherit;
    background: $N1-1000;
    opacity: .65;
    visibility: hidden;
    z-index: 1000;
    -webkit-tap-highlight-color: transparent;

    &::after {
     content: "";
     display: block;
     block-size: 200%;
     inline-size: 200%;
    }
   }

   .drawer {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 1.25rem;
    position: relative;
    background: $N1-0;
    block-size: 100%;
    inline-size: 100%;
    padding-block: 1em;
    z-index: 1001;

    .logo {
     margin-inline: auto;
    }

    figure {
     display: flex;
     block-size: 140px;
     inline-size: 140px;
     overflow: hidden;
     border-radius: 50%;
     margin-inline: auto;

     img {
      display: block;
      object-fit: cover;
      border-radius: inherit;
     }
    }

    ul:last-of-type {
     display: flex;
     align-items: center;
     justify-content: space-around;

     li {
      display: inline-flex;

      a {
       display: inline-flex;

       svg {
        fill: $N1-800;
       }
      }
     }
    }
   }
  }
 }

 &.nav-desktop {
  .container {
   padding-inline: 3.5em;
  }

  .nav-links {
   flex-direction: row;
   gap: 1.5em;
   block-size: 100%;

   li {
    block-size: 100%;

    a {
     font-size: 1em;
     block-size: 100%;

     &::before {
      content: "";
      position: absolute;
      block-size: .125em;
      inline-size: 100%;
      bottom: 0;
      left: 0;
      border-radius: .125em .125em 0 0;
      background: transparent;
     }

     &.active {
      &::before {
       background-color: $A3-600;
      }
     }

     &:not(.active) {
      &:hover {
       color: $A3-400;
       
       &::before {
        background-color: $A3-400
       }
      }
     }
    }
   }
  }
 }
}