@import "colors";

header {
 display: block;
 block-size: 100vh;
 background: $N1-10;

 .container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  padding-inline: .875em;
  block-size: 100%;

  h1 {
   font-size: 2em;
   font-weight: 350;
   letter-spacing: .02em;
   color: $N1-900;
   margin-block-end: .0875em;

   strong {
    font-weight: 600;
    color: $A3-600;
   }
  }

  h2 {
   font-size: 1.5em;
   font-weight: 350;
   letter-spacing: .015em;
   color: $N1-900;
   margin-block-end: 1em;

   strong {
    font-weight: 600;
    color: $A3-600;
   }
  }
 }

 @media screen and (min-width: 768px) {
  .container {
   padding-inline: 3.5em;
  }
 }
}