@import "colors";

.basic-cards {
 display: grid;
 grid-template-columns: 1fr;
 gap: .875em;
 margin-block: 3em;

 .basic-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: .75em;
  text-align: center;
  border-radius: .5em;
  background: $N1-10;
  box-shadow: 0 0 0 1px #0000000d;
  position: relative;
  padding: 1.5em .75em;

  .basic-card-icon {
   display: inline-flex;
   align-items: center;
   justify-content: center;
   block-size: 3.5em;
   inline-size: 3.5em;
   border-radius: 1em;
   padding: .75em;
   background: $A3-100;

   svg {
    fill: $A3-600;
   }
  }

  h4 {
   font-size: 1.25em;
   font-weight: 450;
   letter-spacing: .02em;
   color: $N1-900;
  }
 }

 @media screen and (min-width: 768px) {
  grid-template-columns: repeat(2, 1fr)
 }

 @media screen and (min-width: 992px) {
  grid-template-columns: repeat(3, 1fr)
 }
}

.mini-cards {
 margin-block: 3em;

 .mini-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 1.5em .75em;
  border-radius: .5em;
  background: $N1-10;
  box-shadow: 0 0 0 1px #0000000d;
  position: relative;

  .mini-card-icon {
   display: inline-flex;
   align-items: center;
   justify-content: center;
   block-size: 3.5em;
   inline-size: 3.5em;
   border-radius: 1em;
   padding: .75em;
   background: $A3-100;

   svg {
    fill: $A3-600;
   }
  }
 }
}

.button-cards {
 display: grid;
 grid-template-columns: 1fr;
 gap: .875em;
 margin-block: 3em;

 .button-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  position: relative;
  border-radius: .5em;
  background: $A3-50;
  box-shadow: 0 0 0 1px #0000000d;
  overflow: hidden;

  .button-card-image {
   display: flex;
   position: relative;
   border-radius: inherit;

   img {
    display: block;
    object-fit: cover;
    border-radius: inherit;
    transition: all .25s ease-in-out;
   }
  }

  .button-card-details {
   display: flex;
   flex-direction: column;
   align-items: center;
   justify-content: center;
   gap: .75em;
   text-align: center;
   border-radius: inherit;
   block-size: 100%;
   inline-size: 100%;
   position: absolute;
   inset: 0;
   background: linear-gradient(#04091eb3,#04091ea6);
   z-index: 10;

   h4 {
    font-size: 1.25em;
    font-weight: 450;
    letter-spacing: .02em;
    color: $N1-10;
    text-shadow: 0 1px 0 $N1-900;
   }

   svg {
    fill: $N1-10;
   }
  }

  &:hover {
   .button-card-image {
    img {
     transform: scale(1.25);
    }
   }
  }
 }

 @media screen and (min-width: 768px) {
  grid-template-columns: repeat(2, 1fr)
 }

 @media screen and (min-width: 992px) {
  grid-template-columns: repeat(3, 1fr)
 }
}

.artboard-cards {
 margin-block: 3em;
}