@import "colors";

footer {
 display: block;
 padding-block: 4em 2em;
 background: $N1-10;

 .container {
  padding-inline: .875em;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2em;

  & > a {
   display: inline-flex;
   align-items: center;
   justify-content: center;
   max-inline-size: 300px;
   inline-size: 100%;

   img {
    display: block;
    object-fit: cover;
    filter: invert(.75);
   }
  }

  ul {
   display: flex;
   align-items: center;
   justify-content: space-between;
   max-inline-size: 300px;
   inline-size: 100%;

   li {
    display: inline-flex;

    a {
     display: inline-flex;
     align-items: center;
     justify-content: center;
     position: relative;
     block-size: 3em;
     inline-size: 3em;
     border-radius: 50%;
     border: 1px solid $N1-800;
     user-select: none;
     transition: background-color 150ms linear, border-color 150ms linear;
     -webkit-user-select: none;

     svg {
      fill: $N1-800;
     }

     &:hover {
      background-color: $A3-600;
      border-color: $A3-600;

      svg {
       fill: $N1-0;
      }
     }
    }
   }
  }

  p {
   font-size: .875em;
   color: $N1-800;
  }
 }

 @media screen and (min-width: 768px) {
  .container {
   padding-inline: 3.5em;
  }
 }
}