@import "colors";

section {
 display: block;
 margin-block-end: 4em;
 padding-block-start: 4em;

 .container {
  display: flex;
  flex-direction: column;
  padding-inline: .875em;

  h2 {
   font-size: 2.25em;
   font-weight: 450;
   letter-spacing: .02em;
   text-align: center;
   color: $A3-600;
  }

  h3 {
   font-size: 1.25em;
   font-weight: 400;
   letter-spacing: .02em;
   text-align: center;
   padding-inline: .75em;
   color: $N1-700;
  }
 }

 @media screen and (min-width: 768px) {
  .container {
   padding-inline: 3.5em;
  }
 }
}