@import "colors";

.filled-btn,
.outlined-btn,
.icon-btn {
 display: inline-flex;
 align-items: center;
 justify-content: center;
 text-align: center;
 line-height: 1.5;
 user-select: none;
 transition: background-color 75ms linear;
 -webkit-user-select: none;
}

.filled-btn {
 gap: .375em;
 background-color: $A3-600;
 color: $N1-10;
 font-size: 1.1875em;
 font-weight: 450;
 border: 1px solid $A3-600;
 border-radius: .5em;
 padding: .6em 1.25em;

 svg {
  color: $N1-10;
 }

 &:hover {
  background-color: $A3-400;
  color: $N1-0;

  svg {
   fill: $N1-0;
  }
 }
}

.outlined-btn {
 gap: .375em;
 background-color: transparent;
 color: $A3-600;
 font-size: 1.1875em;
 font-weight: 450;
 border: 1px solid $A3-600;
 border-radius: .5em;
 padding: .6em 1.25em;
 margin-inline: auto;

 svg {
  color: $A3-600;
 }

 &:hover {
  background-color: $A3-600;
  color: $N1-0;

  svg {
   fill: $N1-0;
  }
 }
}

.icon-btn {
 background-color: transparent;
 padding: .625em;
 block-size: 3.25em;
 inline-size: 3.25em;
 border-radius: 50%;

 svg {
  fill: $N1-800;
 }

 &:hover {
  background-color: $N1-50;
 }
}