@import "colors";

form {
 display: flex;
 flex-direction: column;
 inline-size: 100%;
 margin-block: 3em;

 .form-control {
  display: block;
  position: relative;
  border-radius: .325em;
  inline-size: 100%;
  block-size: 4em;
  margin-block-end: 1em;

  input,
  textarea {
   display: block;
   position: absolute;
   block-size: 100%;
   inline-size: 100%;
   padding: .75em .75em .875em;
   letter-spacing: .015em;
   border-radius: inherit;
   color: $N1-900;
   border: 1.5px solid $N1-200;
   font-size: 1em;
   font-weight: 450;
   
   &:focus {
    border: 1.5px solid $A3-600;

    & + label {
     color: $A3-600;
     top: -.725em;
     left: .75em;
     font-size: .7em;
     font-weight: 450;
    }
   }

   &:not(:placeholder-shown) {
    & + label {
     top: -.725em;
     left: .75em;
     font-size: .7em;
     font-weight: 450;
    }
   }
  }

  label {
   position: absolute;
   top: 1.25em;
   left: .65em;
   font-weight: 450;
   z-index: 20;
   color: $N1-500;
   background: $N1-0;
   padding-inline: .2em;
   line-height: 1.5;
   font-size: 1em;
   letter-spacing: .015em;
   transition: all 150ms ease;
  }

  &:last-of-type {
   block-size: 11em;
  }

  @media screen and (min-width: 768px) {
   max-inline-size: 500px;
   inline-size: 100%;
   margin-inline: auto;
  }
 }

 .filled-btn {
  margin-inline: auto;
 }
}